export default {
	data() {
		return {
		}
	},
	onLoad() {

	},
	methods: {
		onAddDevice(page){
			uni.showActionSheet({
				itemList: ['扫一扫', '输入序列号'],
				success: function (res) {
					if(res.tapIndex === 0) {
						uni.scanCode({
							success: function (res) {
								console.log('条码类型：' + res.scanType);
								console.log('条码内容：' + res.result);
							}
						});
					} else {
						uni.navigateTo({
							url: page
						});
					}
				},
				fail: function (res) {
					alert(res.errMsg);
				}
			});
		}

	}
}
